import React from 'react';
import { Link } from 'react-router-dom';

export default ({page, total, topic})  => (
  <div className="Pagination">
    {page>1 &&
      <Link
        className="btn"
        to={`/search/${topic.replace(/ /g, '-')}/${page-1}/`.replace(/[/]1$/,'')}>
        Previous
      </Link> }
    {page==1 &&
      <span className="btn disabled"> Previous </span> }
    <span className="btn" style={{whiteSpace: 'nowrap'}}> {page} / {total} </span>
      {page < total &&
        <Link
          className="btn"
          to={`/search/${topic.replace(/ /g, '-')}/${page+1}/`}>Next</Link> }
      {page >= total && <span className="btn disabled"> Next </span> }
  </div>
);