
import React from 'react';
export default ({analyzedInstructions}) => (
  <div className="steps">
    <h2>Preparation</h2>
    {
      analyzedInstructions.map(x =>
        <div>
          <b>{x.name}</b>
          <ul>
            {x.steps.map( ({step}, i) => <li key={i}>{step}</li> )}
          </ul>
        </div>
      )
    }
  </div>
);