import { useEffect } from 'react';
import { useLocation } from 'react-router';

export default function ScrollToTop() {
  const { pathname } = useLocation();

  useEffect(() => {
    if (!global.window || !global.navigator)
      return;

    if (navigator.userAgent !== "ReactSnap") {
      window.scrollTo(0, 0);
    }
  }, [pathname]);

  return null;
}