import React from 'react';
import { Link } from 'react-router-dom';
import { useLazyData } from '../utils';

export default data => {
  const fetch = useLazyData();
  return (
    <div className="card" onMouseOver={() => fetch('/data/recipes/'+data.id+'.json')}>
      <Link to={'/recipe/'+data.id+'/'} className="image">
        <img src={'/'+data.image} width={300} loading="lazy"/>
      </Link>

      <div className="meta">
        <div className="source">{
          data.sourceName
        }</div>
        <div className="title">{data.title}</div>
      </div>
    </div>
  );
};
