import React, { useEffect } from 'react';
import { Helmet } from 'react-helmet';
import RecipeCard from './RecipeCard';
import Pagination from './Pagination';
import {wait, useData, useLazyData} from '../utils';
import SearchBar from './SearchBar';

const getKeywords = (topic, data) => [
  ...new Set([
    'Recipes',
    topic,
    ...data
      .results
      .flatMap(x=>x.tags)
  ])
].join(', ');

export default function Search(props) {
  const fetch = useLazyData();
  const page =
    props.match && props.match.params.page
    ? parseInt(props.match.params.page)
    : 1;

  const topic = (props.match && props.match.params.topic || 'all').replace(/-/g,' ');

  const {data} = useData(`/data/${topic.replace(/ /g, '-')}/${page-1}.json`);

  useEffect(() => {
    if (data) {
      setTimeout(() => {
        fetch(`/data/${topic.replace(/ /g, '-')}/${page}.json`);
      }, 100);
    }
  }, [page, topic, data]);

  if (!data)
    return null;

  const keywords = getKeywords(topic, data);

  return (
    <>
      <div className="section">
        <Helmet>
          <title>{topic} Recipes</title>
          <meta property="og:title" content={`${topic} Recipes`}/>
          <meta name="keywords" content={keywords} />
          <meta name="description" content={`Search Recipes`} />
        </Helmet>
        <div className="controlsWithCaption" id="search">
          <h2> Search </h2>
          <div className="controls">
            <SearchBar topic={topic} />
            <Pagination page={page} topic={topic} total={data.total} />
          </div>
        </div>

        <div className="grid">{
          data.results
            .map( (recipe, key) =>
              <RecipeCard {...recipe} key={key} />)
        }</div>
        <div className="controlsWithCaption">
          <div>
            <Pagination page={page} topic={topic} total={data.total} />
          </div>
        </div>
      </div>
    </>
  );
}