import React from 'react';
import { useHistory } from 'react-router';
import Select from '../Select';
import useIndex from './useIndex';
import { placeholder, topDiets } from './data';

export default ({topic}) => {
  const index = useIndex();
  const history = useHistory();

  if (!index)
    return null;

  return (
    <div className="search">
      <Select
        placeholder={topic || `TagsSearch eg.: ${placeholder}`}
        options={
          index
            .sort((a, b) => (topDiets[b.name]||0 - topDiets[a.name]||0)  || b.count - a.count)
            .filter(x => x.count > 20)
            .map(x => ({value: x.slug,  label:x.name}))
        }
        onChange = {x => { history.push(`/search/${x.value.replace(/ /g, '-')}`) }} />
    </div>
  );
}