import agent from 'superagent';
import Dataloader from 'dataloader';
import React, { useContext, useEffect, useState } from 'react';

export const wait = ms => new Promise(done=>setTimeout(done, ms));

export const simpleDataClient = (db = {}) => {
  const loader = new Dataloader(urls =>
    Promise.all(urls.map(url=>agent.get(url))), {
    batch: false
  });

  return {
    fetch: url => loader
      .load(url)
      .then( ({body}) =>{
        db[url] = body;
        return body;
      }),
    fromCache: url => db[url]
  }
};

export const DataContext = React.createContext();

export const useLazyData = () => {
  const {fetch, fromCache} = useContext(DataContext);
  return (key) => {
    const cached = fromCache(key);
    if (cached) {
      return cached;
    } else {
      return fetch(key);
    }
  };
}


export const useData = (key) => {
  const {fetch, fromCache} = useContext(DataContext);
  const data = fromCache(key);

  const [result, setResult] = useState({
    loading: !data,
    data
  });

  useEffect(() => {
    //if (!fromCache(key)) {
      fetch(key).then(data => {
        setResult({loading: false, data});
      }).catch(error => {
        setResult({loading: false, error});
      });
    //}
  }, [key]);

  return result;
}