import React, { useEffect, useState } from 'react';
import Nutrition from './Nutrition';
import Ingredients from './Ingredients';
import Steps from './Nutrition/Steps';
import { Helmet } from 'react-helmet';
import { useData, useLazyData } from '../utils';
import { Link } from 'react-router-dom';
import RecipeCard from './RecipeCard';

const blacklist = new Set([
  'Net Carbohydrates',
  'Cholesterol',
]);


export default function Recipe({match}) {
  const fetch = useLazyData();
  const {data} = useData(`/data/recipes/${match.params.id}.json`);
  if (!data)
    return null;


  const top5Nutrients = data
    .nutrition
    .nutrients
    .filter( x => !blacklist.has(x.title))
    .filter( x => x.percentOfDailyNeeds > 100)
    .sort( (a,b) => b.percentOfDailyNeeds - a.percentOfDailyNeeds)
    .slice(0, 5);

  return (
    <>
      <Helmet>
        <title>{data.title}</title>
        <meta property="og:title" content={data.title} />
        <meta name="keywords" content={data.tags.join(', ')} />
        <meta name="description" content={`Preparation Steps and Nutritional Breakdown of ${data.title}. Source: ${data.sourceName}`} />
      </Helmet>
      <div className="header">
        <div  style={{padding: '1em'}}>
          <img src={'/' + data.image}/>
        </div>
        <div>
          <h1>{data.title}</h1>
          <ul className="summaryList">
            <li> <a href={data.sourceUrl}>{data.sourceUrl}</a> </li>
            <li> Servings: <b>{data.servings}</b></li>
            <li> Ready in: <b>{data.readyInMinutes}</b>min </li>
            {data.preparationMinutes != undefined &&
              <li> Preparation time: <b>{data.preparationMinutes}</b>min </li> }
            {data.cookingMinutes != undefined &&
              <li> Cooking time: <b>{data.cookingMinutes}</b>min </li> }
            <li> Tags:
              <div className="flex">{
              data.tags
                .map( x => <Link
                  onMouseEnter={()=>fetch(`/data/${x.replace(/ /g, '-')}/0.json`)}
                  to={`/search/${x.replace(/ /g, '-')}/`}
                  key={x}
                  className="tag">{x} </Link> )
            }</div></li>

          { (top5Nutrients.length != 0) && <li>High in: {top5Nutrients.map(x => <span key={x.title} className="tag">{x.title.replace('Vitamin ', '')} </span> )} </li> }

          </ul>
        </div>
      </div>
      <Nutrition {...data} />
      <div className="center">
        <Ingredients {...data} />
        <Steps {...data} />
      </div>

      <div className="similar">
        <h1> Similar Recipes </h1>
        <div className="grid">{
          data.similar
            .slice(0, 4)
            .map( (x, key) => <RecipeCard {...x} key={key} />)
        }</div>
      </div>
    </>
  );
}